@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap");
* {
  box-sizing: border-box;
  word-break: break-all;
}

body {
  font-family: Inter, Roboto, Oxygen, Fira Sans, Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  color: #2c3e50;
  background: #2c3e50;
  margin: 0;
  line-height: 1.4;
}

hr {
  margin: 1rem 0;
}

a {
  color: #3eaf7c;
  font-weight: 500;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}

h1 {
  font-size: 1.6rem;
  font-weight: 600;
}

h2 {
  font-size: 1.2rem;
  margin: 0;
}

h3 {
  font-size: 1rem;
  margin: 0;
}

.title {
  color: #ffffff;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 10px;
  max-width: 1000px;
}

.nav {
  display: flex;
  padding: 0;
  margin: 0;
  justify-content: space-between;
  align-items: center;
  min-height: 56px;
  background-color: #fff;
  box-sizing: border-box;
  border-bottom: 1px solid #eaecef;
}
.nav__menu {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  gap: 7px;
  line-height: 1.2;
  font-size: 14px;
  list-style: none;
}
.nav__item {
  margin: 0;
  padding: 10px;
}
.nav__link {
  color: #2c3e50;
  text-decoration: none;
}
.nav__link:hover, .nav__link.active {
  border-bottom: 2px solid #3eaf7c;
  color: #000000;
}

@media screen and (min-width: 450px) {
  .nav__menu {
    flex-direction: row;
    gap: 15px;
  }
}
.list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.list__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.list__item:hover {
  background: #eee;
}
.list__link {
  width: 100%;
}

.btn {
  position: relative;
  place-content: center;
  place-items: center;
  width: fit-content;
  border-radius: 99px;
  letter-spacing: 0.05em;
  border: 1px solid #42b983;
  text-decoration: none;
  text-transform: uppercase;
  margin-right: 10px;
  padding: 0.5rem 1.5rem;
  white-space: nowrap;
  font-weight: 700;
  outline: none;
  color: #42b983;
  background: #fff;
  transition: all 0.22s;
}
.btn:hover {
  cursor: pointer;
  opacity: 0.8;
}
.btn--primary {
  background: #42b983;
  color: #fff;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.form__control {
  position: relative;
  outline: none;
  border: 2px solid #ccc;
  display: block;
  width: 100%;
  color: #2c3e50;
  padding: 0.5rem 1.5rem;
  border-radius: 3px;
  font-size: 1rem;
  margin: 0;
  font-family: inherit;
}

.form__textarea {
  resize: none;
}

.form-control.invalid .form__input {
  border-color: #e53935;
}

.form__label {
  display: block;
  margin: 0 0 0.3rem 0.3rem;
  font-weight: 500;
}

.form__input:active,
.form__input:focus {
  transition: border 0.22s;
  border: 2px solid #42b983;
}

.pagination {
  overflow: hidden;
  clear: both;
  margin: 0 0 20px 0;
  margin-bottom: 15px;
}
.pagination__list {
  padding: 0;
  margin: 0;
  text-align: center;
}
.pagination__link {
  display: inline-block;
  height: 28px;
  min-width: 28px;
  line-height: 28px;
  font-size: 15px;
  text-decoration: none;
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 3px;
  background: #fbfbfb;
  text-decoration: none;
  color: #2c3e50;
}
.pagination__link:hover {
  background: #7debb9;
}
.pagination__item {
  display: inline-block;
  margin: 0 2px 3px;
}
.pagination__item.active .pagination__link {
  background: #42b983;
  border-color: #42b983;
}
.pagination.separator .pagination__link {
  border-color: #fff;
  background: #fff;
}
.pagination.disabled .pagination__link {
  color: #999;
}

.request {
  display: flex;
  flex-direction: column;
  align-items: start;
}
.request__title {
  line-height: 1.2;
  margin-bottom: 15px;
}
.request__text {
  word-break: break-all;
}

.card {
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 10px;
  box-shadow: 2px 3px 10px rgba(0, 0, 0, 0.2);
  background: #fff;
}

.alert {
  padding: 1rem 1.5rem;
  border-left-width: 0.5rem;
  border-left-style: solid;
  margin: 1rem 0;
  position: relative;
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
}
.alert.primary {
  border-color: #42b983;
  background-color: #f8f8f8;
}
.alert.warning {
  border-color: #c25205;
  background-color: #f8f8f8;
}
.alert__text {
  color: #222;
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}